* {
	box-sizing: border-box;
}

html {
	font-size: 100%;
}

body {
	background-color: #fff;
}

div {
	position: relative;
}

@font-face {
	font-family: "CaviarDreams"; // by Lauren Thompson - https://www.nymfont.com/
	src: url("/public/fonts/CaviarDreams.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "CaviarDreams";
	src: url("/public/fonts/CaviarDreams_Bold.woff2") format("woff2");
	font-weight: bold;
	font-style: normal;
}

@font-face {
	font-family: "CaviarDreams";
	src: url("/public/fonts/CaviarDreams_Italic.woff2") format("woff2");
	font-weight: normal;
	font-style: italic;
}

@font-face {
	font-family: "CaviarDreams";
	src: url("/public/fonts/CaviarDreams_BoldItalic.woff2") format("woff2");
	font-weight: bold;
	font-style: italic;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	color: #2a137b;
	text-transform: uppercase;
	font-weight: 700;
	margin: 0 0 1em;
}

h1 {
	font-size: 3.2rem;
	text-align: right;
	border-bottom: 2px solid #2a137b;
	margin: 10px 0 1em;
}

h2 {
	font-size: 2.6rem;
}

h3 {
	font-size: 2rem;
}

h4 {
	font-size: 1.6rem;
}

h5,
h6 {
	font-size: 1.2rem;
}

p,
div,
span,
li,
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: "CaviarDreams", sans-serif;
}

p {
	margin: 0 0 1em;
	font-size: 1.8rem;
	font-weight: 400;
	text-align: justify;
	line-height: 1.4;
}

.replace {
	text-indent: -10000px;
	display: block;
	overflow: hidden;
	background-repeat: no-repeat;
	background-position: left top;
	background-color: rgb(0 0 0);
}

.replace * {
	text-indent: -10000px;
	display: block;
	overflow: hidden;
	background-repeat: no-repeat;
	background-position: left top;
	background-color: rgb(0 0 0);
}

.replace a {
	width: 100%;
	height: 100%;
}

.replace a span {
	text-indent: -10000px;
}

.cf::before,
.cf::after {
	content: " ";
	display: table;
}

.cf::after {
	clear: both;
}

.cf {
	*zoom: 1;
}

.clearfix {
	float: none;
	clear: both;
}

.js-only {
	display: none;
}

.js .js-only {
	display: block;
}

.js span.js-only {
	display: inline;
}

html.js .js-frame {
	background-repeat: no-repeat;
	background-position: 0 0;
	background-size: cover;
}

.container {
	width: 100%;
	max-width: 960px;
	margin: auto;
}

.main {
	min-height: 540px;
}

.centred-image {
	display: block;
	margin: auto;
}

html.release {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
}

html.release body {
	background-color: #000;
	color: #ffff7a;
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
}

html.release body a {
	color: #fff;
}

html.release body footer a {
	color: #fff;
	letter-spacing: 1px;
}

html.release body a:hover {
	color: #ffff7a;
}

html.release body .wrapper {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
}

html.release body h1 {
	display: none;
	border: none;
	margin: 0 auto 20px;
	padding: 0;
	text-align: center;
}

html.release body h1 img {
	width: calc(100% - 10px);
	max-width: 1032px;
	margin: auto;
}

html.release body h2,
html.release body h3 {
	margin: 20px auto;
	color: #ffff7a;
	letter-spacing: 2px;
	font-weight: normal;
}

html.release body h3 {
	margin-bottom: 5px;
}

html.release body main {
	text-align: center;
	padding-top: 20px;
}

html.release body main img {
	display: block;
	margin: auto;
	border-radius: 10px;
	box-shadow: 2px 2px 5px rgba(255 255 255 / 50%);
	width: calc(100% - 10px);
	max-width: 500px;
}

html.release body main ul li {
	font-size: 1.5rem;
	list-style: none;
}

html.release body main .releaseLinks {
	list-style: none;
	padding: 0;
	font-size: 1.5rem;
}

html.release body main .releaseLinks li {
	padding: 0;
	margin: 0;
}

html.release body main .releaseLinks li a {
	display: block;
	margin: 0 auto 5px;
	padding: 5px 0;
	width: 100%;
	max-width: 200px;
	color: #000;
	text-decoration: underline;
	background-color: #ffff7a;
	border-radius: 15px;
}

html.release body main .releaseLinks li a:hover {
	background-color: #fff;
	color: #2a137b;
}

html.release body footer {
	border: none;
	padding: 20px 0;
	margin: 0;
	text-align: center;
}

html.release body footer p {
	color: #ffff7a;
	font-weight: normal;
	margin-top: 10px;
	text-align: center;
	text-transform: initial;
}
